import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		seller: {},
		goods: [],
	},
	mutations: {
		getSeller(state,response){
			state.seller = response
		},
		getGoods(state,response){
			state.goods = response
		}
	},
	actions: {
		getSeller(context){
			axios.get('../.././static/seller.json')
			.then((response) => {
				context.commit('getSeller',response.data.seller)
			})
		},
		getGoods(context){
			axios.get('../.././static/goods.json')
			.then((response) => {
				context.commit('getGoods',response.data.goods);
			})
		},
	}
})

export default store


