import Vue from 'vue'
import Router from 'vue-router'
import Goods from 'components/Goods/Goods'
import Ratings from 'components/Ratings/Ratings'
import Seller from 'components/Seller/Seller'

Vue.use(Router)
// var router1 = new Router()
// router1.go(Ratings)

export default new Router({
  routes: [
		{
			path: '/',
			redirect: Goods//这里是设置默认的路由
		},
		{
			path: '/goods',
			name: 'Goods',
			component: Goods
		},
				{
			path: '/ratings',
			name: 'Ratings',
			component: Ratings
		},
				{
			path: '/seller',
			name: 'Seller',
			component: Seller
		}
  ],
	mode: "history",
	linkActiveClass: "active",//配置link-active-class的别名为active
})



