<template>
	<div id="Header">
		<div class="content-wrapper">
			<div class="avatar">
				<img width="64px" height="64px" :src="seller.avatar">
			</div>
			<div class="content">
				<div class="title">
					<span class="brand"></span>
					<span class="name">{{seller.name}}</span>
					</div>
				<div class="description">
					{{seller.description}}/{{seller.deliveryTime}}分钟送达
				</div>
				<div class="support" v-if="seller.supports"><!-- 这里一定要加v-if因seller可能为空值，则无法渠道值会报错 （在Vue组件中获得数据的方式是一个异步过程，最开始初始化的时候数据为空值，所以传过来的seller值为空值）-->
					<span class="icon" :class="iconMap[seller.supports[0].type]"></span>
					<span class="text">{{seller.supports[0].description}}</span>
				</div>
			</div>
			<div v-if="seller.supports" class="support-count" @click="clickShow">
				<span class="count">{{seller.supports.length}}个</span>
				<i class="icon-keyboard_arrow_right"></i>
			</div>
		</div>
		<div class="bulletin-wrapper" @click="clickShow" >
			<span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
			<i class="icon-keyboard_arrow_right"></i>
		</div>
		<div class="background">
			<img :src="seller.avatar" width="100%" height="100%">
		</div>
		<transition name="fade">
			<div class="detail" v-show="isShow">
				<div class="detail-wraper clearfix">
					<div class="detail-main">
						<h1 class="name">{{seller.name}}</h1>
						<div class="stars-wrapper">
							<Stars :size="48" :score="seller.score"></Stars>
						</div>
						<div class="title">
							<div class="line"></div>
							<div class="text">优惠信息</div>
							<div class="line"></div>
						</div>
						<ul class="supports" v-if="seller.supports">
							<li class="supports-item" v-for="(support,index) in seller.supports" :key="index">
								<span class="icon" :class="iconMap[seller.supports[index].type]"></span>
								<span class="text" >{{support.description}}</span>
							</li>
						</ul>
						<div class="bulletin">
							<div class="line"></div>
							<div class="text">商家公告</div>
							<div class="line"></div>
						</div>
						<div class="bulletin-content">
							<span class="bulletin">{{seller.bulletin}}</span>
						</div>
					</div>
				</div>
				<div class="detail-close" @click="clickClose">
					<i class="icon-close"></i>
				</div>
			</div>
		</transition>
	</div>
</template>

<script type="text/ecmascript-6">
	import Stars from '../Stars/Stars.vue'
	
	export default{
		name: "Header",
		components: {
			Stars
		},
		props: {

		},
		created() {
			this.iconMap = ['decrease','discount','guarantee','invoice','special'];
			this.$store.dispatch('getSeller');
		},
		data () {
			return {
				isShow: false,
			}
		},
		methods: {
			clickShow(){
				this.isShow=true;
			},
			clickClose(){
				this.isShow = false;
			}
		},
		computed: {
			seller() {
				return this.$store.state.seller;
			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
	@import './Header.styl'
</style>



