<template>
	<div id="Shopcart">
		<div class="content">
			<div class="left">
				<div class="logo-wrapper">
					<div class="logo" :class="{'hight-light': selectFoodsCount > 0}">
						<span class="icon-shopping_cart" :class="{'hight-light': selectFoodsCount > 0}"></span>
					</div>
					<div class="total" v-show="selectFoodsCount > 0">{{selectFoodsCount}}</div>
				</div>
				<div class="price" :class="{'hight-light': totalPrice > 0}">¥{{totalPrice}}</div>
				<div class="desc">另需配送费￥{{seller.deliveryPrice}}元</div>
			</div>
			<div class="right" :class="{'hight-light': totalPrice >= seller.minPrice}">{{totalDesc}}</div>
		</div>
	</div>
</template>

<script type="text/ecmascript-6">
	
	export default{
		name: "Shopcart",
		props: {
			selectFoods: {
				type: Array,
			},
		},
		data () {
			return {
				
			}
		},
		methods: {
			
		},
		computed: {
			goods() {
				return this.$store.state.goods;
			},
			seller() {
				return this.$store.state.seller;
			},
			selectFoodsCount() {
				let count=0;
				this.selectFoods.forEach((food) => {
					if(food.count) {
						count += food.count;
					}
				});
				return count;
			},
			totalPrice() {
				let total = 0;
				this.selectFoods.forEach((food) => {
					if(food.count) {
						total += food.price * food.count; 
					}
				});
				return total;
			},
			totalDesc() {
				if(this.totalPrice === 0) {
					return `¥${this.seller.minPrice}起送`;
				}
				else if(this.totalPrice < this.seller.minPrice) {
					return `还差¥${this.seller.minPrice - this.totalPrice}起送`;
				}
				else {
					return "去结算";
				}
			}
		},
		created() {
			this.$store.dispatch('getGoods');
			this.$store.dispatch('getSeller');
			
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
	@import './Shopcart.styl'
	
</style>



