<template>
	<div id="Goods">
		<div class="right-type" ref="rightType">
			<ul class="menu-table">
				<li v-if="goods" class="table-item " v-for="(good,index) in goods" :key="index" :class="{'current' : currentIndex===index}" @click="selectMenu(index,$event)">
					<div class="iconText border-1px"><span v-if="good.type > 0" class="icon" :class="classMap[good.type]"></span><span class="text">{{good.name}}</span></div>
				</li>
			</ul>
		</div>
		<div class="left-detail" ref="leftDetail">
			<ul class="foods-type">
				<li class="good good-list-hook" v-for="(good,index) in goods" :key="index">
					<h1 class="type-name">{{good.name}}</h1>
					<ul class="foods">
						<li class="food border-1px" v-for="(food,index) in good.foods" :key="index">
							<img :src="food.image" width="60px" height="60px">
							<div class="food-description">
								<h1 class="name">{{food.name}}</h1>
								<span class="description" v-if="food.description">{{food.description}}</span>
								<span class="sell-count">月售{{food.sellCount}}份</span><span class="rating">好评率{{food.rating}}%</span>
								<div class="new-old">
									<div class="price">
										<span class="newPrice">¥{{food.price}}</span><span class="oldPrice" v-if="food.oldPrice">{{food.oldPrice}}</span>
									</div>
									<div class="cartControl-wrapper">
										<cartControl :food="food" @addCount = "addCount($event)"></cartControl>
									</div>
									</div>
							</div>
						</li>
					</ul>
				</li>
			</ul>
		</div>
		<shopcart :selectFoods="selectFoods"></shopcart>
	</div>
</template>

<script type="text/ecmascript-6">
	import Vue from 'vue'
	import BetterScroll from 'better-scroll'
	import Shopcart from 'components/Shopcart/Shopcart'
	import CartControl from 'components/CartControl/CartControl'
	
	export default{
		name: "Goods",
		props:{
			/*seller: {
				type: Object
			}*/
		},
		data () {
			return {
				classMap: ['decrease','discount','guarantee','invoice','special'],
				listHeight: [],
				scrollY: 0,
			};
		},
		components: {
			Shopcart,CartControl,
		},
		created() {
			this.$store.dispatch('getGoods');
			this.$nextTick(() => {
				this._initScroll();
				this._calculate();
			});
			/* 
			<!-- this.$http.get('../../../static/goods.json')
			.then(rep => {
				this.goods=rep.data.goods;
				//console.log(this.seller);
				this.$nextTick(() => {
					this._initScroll();
					this._calculate();
				});
			}) -->*/
		},
		updated() {

		},
		methods: {
			_initScroll() {
				this.rightType = new BetterScroll(this.$refs.rightType, {
					click: true
				});
				this.leftDetail = new BetterScroll(this.$refs.leftDetail, {
					probeType: 3,
					//probeType: 1 滚动的时候会派发scroll事件，会截流。2滚动的时候实时派发scroll事件，不会截流。 3除了实时派发scroll事件，在swipe的情况下仍然能实时派发scroll事件
					click: true
				});
				this.leftDetail.on('scroll',(pos) => {
					this.scrollY=Math.abs(Math.ceil(pos.y));
					//console.log(this.scrollY);
				})
			},
			_calculate(){
				let goodList = this.$refs.leftDetail.getElementsByClassName('good-list-hook');
				// console.log(goodList);
				let height = 0;
				this.listHeight.push(height);
				for (let i = 0,height = 0; i < goodList.length; i++) {
					height += goodList[i].offsetHeight;
					this.listHeight.push(height);
				}
				//console.log(this.listHeight);
			},
			selectMenu(index,event) {
				let goodList = this.$refs.leftDetail.getElementsByClassName('good-list-hook');
				let el = goodList[index];
				this.leftDetail.scrollToElement(el,300);//				<!-- scrollToElement(el, time, offsetX, offsetY, easing)滚动到某个元素，el（必填）表示 dom 元素，time 表示动画时间，offsetX 和 offsetY 表示坐标偏移量，easing 表示缓动函数 -->
			},
			addCount(food) {
				this.goods.forEach((good) => {
					good.foods.forEach((food1) => {
						if((food1.name === food.name) && (food1.info === food.info)) {
							food1.count = food.count;
						}
					})
				})
			}
		},
		computed: {
			currentIndex() {
				for (let i = 0;i < this.listHeight.length; i++) {
					if(!this.listHeight[i+1] || (this.scrollY >= this.listHeight[i] && this.scrollY < this.listHeight[i+1])) {
						return i;
					}
				}
				return 0;
			},
			goods() {
				return this.$store.state.goods;
			},
			selectFoods() {
				let selectFoods = [];
				this.goods.forEach((good) => {
					good.foods.forEach((food) => {
						if(food.count) {
							selectFoods.push(food);
						}
					})
				});
				return selectFoods;
			}
		},
		updated() {
			
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
	@import './Goods.styl'
</style>



